@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,700;0,900;1,200;1,300;1,400;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
    font-family: 'Source Code Pro', monospace;
}

body{font-size:16px}header{min-height:100vh;background:#353535;color:#f9f9f9}h1{color:#f9f9f9;font-weight:700}h1 span{display:block;color:#f95959}p.sub-h1{background:#f95959;color:#f9f9f9;font-family:"Source Sans Pro",sans-serif;font-size:1.5rem;font-weight:700;text-transform:uppercase}
.contact-code-block{max-width:70rem}.contact-code-block .code-styled{background:#353535;color:#f9f9f9}.contact-code-block .option-inline{cursor:pointer;color:#353535;background:#f95959;margin-right:1rem}.contact-code-block .option-inline::before{content:"["}.contact-code-block .option-inline::after{content:"]\a";white-space:pre}span.code-cursor{animation:cursorblink .65s linear infinite}@keyframes cursorblink{0%{color:inherit}50%{color:transparent}}
